<template>
	<gui-page>
		<template v-slot:gBody>
			<!-- 轮播图  -->
			<view>
				<gui-swiper :swiperItems="swiperItems" :spacing="0" :padding="0" borderRadius="0rpx" :width="750"
					:height="360"></gui-swiper>
			</view>
			<!-- 商品标题 分享按钮 -->
			<view
				class="gui-padding gui-flex gui-row gui-space-between gui-align-items-center gui-bg-white gui-dark-bg-level-3"
				style="padding-bottom: 0;">
				<text class="gui-h5 gui-block product-name gui-block">{{product.title}}</text>
				<view class="product-share" @tap="addLove">
					<text class="product-share gui-icons gui-color-orange" v-if="!product.love">&#xe6ad;</text>
					<text class="product-share gui-icons gui-color-orange" v-if="product.love">&#xe605;</text>

				</view>
			</view>
			<!-- 价格 -->
			<view class="gui-padding gui-bg-white gui-dark-bg-level-3" style="padding-top: 0;">
				<view class="gui-flex gui-row gui-nowrap gui-align-items-center">
					<text class="product-price">{{product.price}}积分</text>
				</view>

			</view>


			<!-- 切换导航 -->
			<view class="gui-padding">
				<gui-switch-navigation :items="navItems" :isCenter="true" :size="200" lineHeight="80rpx"
					textAlign="center" activeLineWidth="200rpx" activeLineHeight="2rpx" :margin="10"
					@change="navChange"></gui-switch-navigation>
			</view>
			<!-- 详情 请根据项目情况 可以使用 富文本/图片-->
			<view class="gui-padding" v-if="active == 0">
				<!-- <image style="width:690rpx;" :src="item.img" mode="widthFix" v-for="(item,index) in product.imgs"
					:key="index"></image> -->
				<rich-text :nodes="nodes"></rich-text>
			</view>
			<!-- 评论 请复制我们提供的评论布局来实现 -->
			<view class="gui-padding gui-margin-top" v-if="active == 1">
				<view>
					<view style="margin-top:30rpx;"
						class="gui-comments-items gui-flex gui-row gui-nowrap gui-space-between gui-bg-white gui-dark-bg-level-3"
						v-for="(item, index) in product.shopComment" :key="index">
						<image :src="getface()" class="gui-comments-face"></image>
						<view class="gui-comments-body">
							<view class="gui-flex gui-row gui-nowrap gui-space-between gui-align-items-center">
								<text class="gui-comments-header-text gui-text gui-primary-color">{{item.userName}}</text>
								
							</view>
							<text class="gui-comments-content gui-block">{{item.content}}</text>


							<view class="gui-comments-info gui-flex gui-rows gui-nowrap gui-space-between gui-align-items-center">
								
								<text class="gui-comments-info-text gui-color-gray">{{item.createTime}}</text>
							</view>
						</view>
					</view>
				</view>
			</view>



			<!-- 底部 -->
			<view class="product-footer gui-bg-white gui-dark-bg-level-3">


				<!-- 底部按钮栏 -->
				<view class="gui-flex gui-row gui-space-between gui-align-items-center gui-border-t">
					<!-- 2个底部按钮 -->
					<!-- <view class="gui-footer-icon-buttons" style="margin-left:30rpx;" hover-class="gui-tap"
						@tap="goHome">
						<text class="gui-footer-icon-buttons-icon gui-block gui-icons gui-primary-color">&#xe63b;</text>
						<text class="gui-footer-icon-buttons-text gui-block gui-icons gui-primary-color">首页</text>
					</view> -->

					<!-- 2个大按钮 -->
					<view
						class="gui-flex1 gui-footer-large-buttons gui-flex gui-rows gui-nowrap gui-justify-content-end">
						<view class=" gui-footer-large-button gui-text-center gui-bg-add-card" @tap="goHome"
							style="border-bottom-left-radius:50rpx; border-top-left-radius:50rpx;"
							hover-class="gui-tap">


							<text
								class="gui-text gui-color-white gui-footer-icon-buttons-icon gui-icons gui-footer-large-button-text gui-block-text gui-text-center">
								&#xe63b; 首页</text>
						</view>
						<view class="gui-footer-large-button gui-text-center gui-bg-buy" @tap="buynow"
							style="border-bottom-right-radius:50rpx; border-top-right-radius:50rpx;"
							hover-class="gui-tap">
							<text
								class="gui-text gui-color-white gui-footer-large-button-text gui-block-text gui-text-center">立即购买</text>
						</view>
					</view>
				</view>
				<gui-iphone-bottom></gui-iphone-bottom>
			</view>
			<view>
				<!-- 提示窗示例 -->
				<uni-popup ref="alertDialog" type="dialog">
					<uni-popup-dialog cancelText="取消" confirmText="确定" title="提示" content="确定购买该商品吗!"
						@confirm="dialogConfirm" @close="dialogClose"></uni-popup-dialog>
				</uni-popup>
			</view>
			<view>
				<!-- 提示窗示例 -->
				<uni-popup ref="alertSuccess" type="dialog">
					<uni-popup-dialog confirmText="确定" title="提示" content="购买成功!" @confirm="goOrder"></uni-popup-dialog>
				</uni-popup>
			</view>
			<view>
				<!-- 提示窗示例 -->
				<uni-popup ref="alertError" type="dialog">
					<uni-popup-dialog confirmText="确定" title="提示" content="积分不足请充值!"></uni-popup-dialog>
				</uni-popup>
			</view>
			<!-- 底部占位 -->
			<view style="height:120rpx;"></view>
		</template>
	</gui-page>
</template>
<script>
	var graceJS = require('@/GraceUI5/js/grace.js');
	var request = require('@/GraceUI5/js/request/request.js');
	var parserHtml = require("@/GraceUI5/js/parserHTML.js");

	export default {
		data() {
			return {
				// 轮播图 
				swiperItems: [{
						img: 'https://img30.360buyimg.com/sku/jfs/t21838/18/2275707529/311540/cba1d04c/5b4f155fNac3aa2f0.jpg',
						url: '',
						opentype: 'navigate'
					},
					{
						img: 'https://img30.360buyimg.com/sku/jfs/t22021/327/2281785192/48707/57806074/5b4f1579Nae7adb49.jpg',
						url: '',
						opentype: 'navigate'
					}
				],
				// 商品信息
				product: {
					name: "小米 MIX3 一面科技 一面艺术 ( 磁动力滑盖全面屏 | 故宫特别版 )",
					logo: "../../static/logo.png",
					price: 3188,
					priceMarket: 3200,
					imgs: [
						'https://img30.360buyimg.com/sku/jfs/t21838/18/2275707529/311540/cba1d04c/5b4f155fNac3aa2f0.jpg',
						'https://img30.360buyimg.com/sku/jfs/t22021/327/2281785192/48707/57806074/5b4f1579Nae7adb49.jpg',
						'https://img30.360buyimg.com/sku/jfs/t21682/256/2344553276/204456/cf7a2ddb/5b4ffbbfN48c54307.jpg'
					]
				},
				// 切换导航
				navItems: [{
					id: 0,
					name: '商品详情'
				}, {
					id: 1,
					name: '相关评论'
				}],
				// 切换索引
				active: 0,
				// 属性选择
				attrShow: false,
				colors: [{
						id: 1,
						text: "红色",
						checked: false
					},
					{
						id: 2,
						text: "黑色",
						checked: false
					},
					{
						id: 3,
						text: "蓝色",
						checked: false
					}
				],
				sizes: [{
						id: 1,
						text: "10 cm",
						checked: false
					},
					{
						id: 2,
						text: "20 cm",
						checked: false
					},
					{
						id: 3,
						text: "40 cm",
						checked: false
					}
				],
				// 属性记录
				attrRes: {
					color: null,
					size: null,
					number: 1
				}
			}
		},
		onLoad(e) {
			console.log(e.id)
			this.getShop(e.id)


		},
		methods: {
			getface(){
				      let str = '/static/face(' + Math.floor(Math.random() * 8) + ').png'
						return str
			},
			getShop(id) {
				uni.gRequest.post("bohe/getShopById", {
					data: {
						id: id,
						userId: graceJS.getStorage("id")
					}
				}, (res) => {
					console.log(res)
					this.product = res.data.data
					console.log(this.product)
					let imglist = this.parseImgsFromString(this.product.imgs)
					console.log(imglist)
					this.swiperItems = []
					for (let i of imglist) {
						console.log(i)
						this.swiperItems.push({
							img: 'http://192.168.5.2/dev-api' + i
						})
					}
					this.nodes = parserHtml.parserHTML(this.product.content);

					this.product.imgs = this.swiperItems
					// this.swiperItems = imglist
					console.log(this.swiperItems)
				})
			},
			// 数组 → 字符串
			formatImgsToString(arr) {
				try {
					console.log("222")

					return arr.length ? arr.join(',') : '';
				} catch (e) {

					return arr
				}
			},
			// 字符串 → 数组
			parseImgsFromString(str) {
				try {
					console.log(str)
					const imgArray = str.split(',')
						.map(path => path.trim()) // 去除两端空格
						.filter(path => path !== ''); // 过滤空字符串


					return imgArray;
				} catch (e) {
					return str
					//TODO handle the exception
				}

			},
			// 分享
			share: function() {
				uni.showToast({
					title: '请自行完善分享代码',
					icon: "none"
				});
			},
			// 导航切换
			navChange: function(e) {
				this.active = e;
			},
			// 返回首页
			goHome: function() {
				uni.switchTab({
					url: "/pages/switchPages/index"
				});
			},


			// 购买 
			buynow: function() {
				this.$refs.alertDialog.open()
			},

			goOrder() {
				uni.navigateTo({
					url: "./order"
				})
			},

			dialogConfirm() {
				console.log("11")
				uni.gRequest.post("bohe/createorder", {
					data: {
						shopId: this.product.id,
						userId: graceJS.getStorage("id")
					}
				}, (res) => {
					if (res.data.code == 200) {
						this.$refs.alertSuccess.open()
					} else {
						this.$refs.alertError.open()
					}

					console.log(res)
				})
			},
			//添加收藏/取消
			addLove() {

				uni.gRequest.post("bohe/addloveShop", {
					data: {
						shopId: this.product.id,
						uid: graceJS.getStorage("id")
					}
				}, (res) => {
					this.getShop(this.product.id)


				})
			},

		}
	}
</script>
<style scoped>
	.product-name {
		width: 560rpx;
		line-height: 50rpx;
	}

	.product-share {
		width: 80rpx;
		height: 80rpx;
		text-align: center;
		font-size: 50rpx;
		color: #FF7900;
		line-height: 80rpx;
	}

	.product-price {
		color: #FF7900;
		line-height: 60rpx;
		font-size: 30rpx;
		font-weight: bold;
	}

	.gui-common-line {
		height: 18rpx;
	}

	.product-footer {
		position: fixed;
		z-index: 99;
		left: 0;
		bottom: 0;
		width: 750rpx;
	}

	.product-attr {
		width: 700rpx;
		margin: 25rpx;
		height: 580rpx;
	}

	.gui-padding {
		padding: 20rpx 25rpx;
	}

	.gui-footer-icon-buttons {
		width: 100rpx;
	}
</style>